

body {
    background: #4242444d;
}


.todo-container {
    background: rgba(218, 229, 255, 0.65);
    max-width: 90%;
    min-width: 45%;
    height: 70%;
    max-height: 90%;
    margin: 20px;
    overflow: hidden;
}


.input-container {
    background-color: #4f4fda;
    padding: 15px;
}

.input-field {
    border: none;
    -webkit-box-shadow: 0px 0px 1px inset rgba(0, 0, 0, 0.472);
            box-shadow: 0px 0px 1px inset rgba(0, 0, 0, 0.472);
    width: 100%;
    height: 45px;
    background-color: #17179cdb;
    padding: 0 10px;
    color: white;
    font-weight: bold;
    -webkit-box-sizing: border-box;
            box-sizing: border-box;
    outline: none;
}

.input-field::-webkit-input-placeholder{
    color: rgba(255, 255, 255, 0.322)
}

.input-field:-ms-input-placeholder{
    color: rgba(255, 255, 255, 0.322)
}

.input-field::-ms-input-placeholder{
    color: rgba(255, 255, 255, 0.322)
}

.input-field::placeholder{
    color: rgba(255, 255, 255, 0.322)
}




